import { Formik, Form, Field, ErrorMessage } from "formik"
import style from "./RegistPage.module.css"
import * as Yup from "yup"
import YupPassword from "yup-password"
import Logo from "../../Logo"
import axios from "axios"
import { Link, useNavigate } from "react-router-dom"
import { useState } from "react"
import { useDispatch } from "react-redux"
import { setUser } from "../../../store/slice"
import passNo from "./../../../Images/passNo.png"
import passYes from "./../../../Images/passYes.png"
import { useRef } from "react"
import { URLS } from "../../../urls/urlsAndDates"

YupPassword(Yup)

const RegistPage = () => {
  const [reply, setReply] = useState("")
  const [passwordType, setPasswordType] = useState(passNo)
  const [confirmType, setConfirmType] = useState(passNo)

  const dispatch = useDispatch()
  const navigate = useNavigate()
  const passRef = useRef(null)
  const confirmRef = useRef(null)
  const togglePassInput = () => {
    if (passwordType === passNo) {
      passRef.current.type = "text"
      setPasswordType(passYes)
    } else if (passwordType === passYes) {
      passRef.current.type = "password"
      setPasswordType(passNo)
    }
  }

  const toggleConfirmInput = () => {
    if (confirmType === passNo) {
      confirmRef.current.type = "text"
      setConfirmType(passYes)
    } else if (confirmType === passYes) {
      confirmRef.current.type = "password"
      setConfirmType(passNo)
    }
  }

  const registerHandler = async (values, { setSubmitting }) => {
    setPasswordType(passNo)
    setConfirmType(passNo)
    const payload = {
      email: values.email,
      username: values.username,
      password: values.password,
    }
    const payload2 = {
      username: values.username,
      password: values.password,
    }
    try {
      const response = await axios.post(URLS.registration, payload)

      response.data.email && setReply("  ")
      axios.post(URLS.authorisation, payload2).then((response2) => {
        dispatch(
          setUser({
            token: response2.data.auth_token,
          })
        )
        response.data.auth_token &&
          setReply(
            ` ${payload.username}     `
          )

        navigate("/rectangle")
      })
    } catch (e) {
      console.log(e)
      e.response.data.username
        ? setReply(`   ${payload.username}  `)
        : setReply(`   Email  `)
    } finally {
      setSubmitting(false)
    }
  }

  const validationSchema = Yup.object({
    email: Yup.string()
      .email("  ")

      .required(" "),
    username: Yup.string()
      .matches(/^[A-Za-z0-9_-]+$/, "       ")
      .min(6, "    6   ")
      .max(32, "    6  32 ")
      .required(" "),
    password: Yup.string()
      .matches(/^[A-Za-z0-9_-]+$/, "  ")
      .required(" ")
      .min(
        6,
        "    6   ,             "
      )

      .minNumbers(
        1,
        "    6   ,             "
      )
      .minUppercase(
        1,
        "    6   ,             "
      )
      .max(32, "  32 "),
    confirmPassword: Yup.string()
      .oneOf([Yup.ref("password")], "  ")
      .required(" "),
  })

  return (
    <div className={style.root}>
      <div className={style.formRegist}>
        <div className={style.logo}>
          <Logo />
        </div>
        <div className={style.regist}>
          <h1></h1>
          <Formik
            initialValues={{
              email: "",
              username: "",
              password: "",
            }}
            validateOnBlur
            validationSchema={validationSchema}
            onSubmit={registerHandler}
          >
            {({ isValid, dirty, isSubmiting, values, errors }) => (
              <Form className={style.form} onChange={() => setReply("")}>
                <label> . </label>
                <Field
                  type="email"
                  name="email"
                  className={
                    values.email && errors.email
                      ? style.inputError
                      : style.input
                  }
                  placeholder={"  . ..."}
                />

                <div className={style.error}>
                  {values.email && errors.email}
                </div>

                {!values.email && (
                  <ErrorMessage
                    name="email"
                    component="div"
                    className={style.error}
                  />
                )}
                <br />
                <label></label>
                <Field
                  type="username"
                  name="username"
                  className={
                    values.username && errors.username
                      ? style.inputError
                      : style.input
                  }
                  placeholder={" ..."}
                />

                <div className={style.error}>
                  {values.username && errors.username}
                </div>
                {!values.username && (
                  <ErrorMessage
                    name="username"
                    component="div"
                    className={style.error}
                  />
                )}
                <br />
                <label></label>
                <div className={style.pass}>
                  <Field
                    type="password"
                    name="password"
                    className={
                      values.password && errors.password
                        ? style.inputError
                        : style.input
                    }
                    placeholder={" ..."}
                    innerRef={passRef}
                  />
                  <img
                    className={style.icon}
                    src={passwordType}
                    onClick={() => togglePassInput()}
                    alt="eye"
                  ></img>
                  <div className={style.error}>
                    {values.password && errors.password}
                  </div>
                  {!values.password && (
                    <ErrorMessage
                      name="password"
                      component="div"
                      className={style.error}
                    />
                  )}
                </div>
                <br />
                <label> </label>
                <div className={style.pass}>
                  <Field
                    type="password"
                    name="confirmPassword"
                    className={
                      values.confirmPassword && errors.confirmPassword
                        ? style.inputError
                        : style.input
                    }
                    placeholder={"  ..."}
                    innerRef={confirmRef}
                  />
                  <img
                    className={style.icon}
                    src={confirmType}
                    onClick={() => toggleConfirmInput()}
                    alt="eye"
                  ></img>
                  <div className={style.error}>
                    {values.confirmPassword && errors.confirmPassword}
                  </div>
                  {!values.confirmPassword && (
                    <ErrorMessage
                      name="confirmPassword"
                      component="div"
                      className={style.error}
                    />
                  )}
                </div>
                <br />
                <p className={style.textReg}>
                  <br />
                  <Link to="/" className={style.reg}>
                    {" "}
                      
                  </Link>
                </p>
                <div className={style.only_lat_message}>* </div>
                <div className={style.reply}>{reply}</div>
                <button
                  className={style.btn}
                  type={"submit"}
                  disabled={!(isValid && dirty)}
                >
                  
                </button>
              </Form>
            )}
          </Formik>
        </div>
      </div>
      <div className={style.registFon}></div>
    </div>
  )
}

export default RegistPage
